//common styles
body {
    margin: 0;
    padding:0;
}

// main menu

.main-page-wrapper {
    height: 100vh;
    display:flex;
    overflow: hidden;
    position: relative;
}

.main-page-wrapper-active {
    overflow: auto;
}

.first-menu-wrapper {
    width: 100%;
    display: flex;
    left: 0%;
    position: fixed;
    height: 100vh;
    transition: left 0.6s ease-in-out;
}
.main-page-wrapper-active .first-menu-wrapper {
    left: -50%;
}

//left menu
.left-side {
    width: 50%;
    height: 100%;
    background-color: #0d2226;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: width .6s ease-in-out;
}
.right-side {
    width: 50%;
    height: 100%;
    background-color: #d46f4d;
    overflow: hidden;
    transition: width 0.6s ease-in-out;
}
.main-page-wrapper-active .right-side {
    width: 24%;
}
.left-menu button{
    display: block;
    height: 50px;
    width: 200px;
    font-family: 'Poppins', sans-serif;
    font-weight: 900;
    text-transform: uppercase;
    font-size: 11px;
    letter-spacing: 2px;
    word-spacing: 2px;
    cursor: pointer;
}
.left-menu-contact {
    margin-bottom: 25px;
    background: transparent;
    border: 3px solid #b4b4b4;
    color: #fff;
    transition: all .4s;
    box-shadow: 0px 18px 40px 0px rgba(0,0,0,0.75);
    outline: none;
    transition: all .6s ease-in-out;
}
.left-menu-contact:hover{
    background: #fff;
    border: 3px solid #fff;
    color: #0d2226;
    box-shadow: 0 11px 27px 0 rgba(0,0,0,.75);
}
.left-menu-resume {
    color: #191919;
    background: #fff;
    transition: all .6s ease-in-out;
    border: 3px solid #fff;
    box-shadow: 0 11px 27px 0 rgba(0,0,0,.75);
}
.left-menu-resume:hover{
    background: transparent;
    border: 3px solid #fff;
    color: #fff;
    box-shadow: 0px 18px 22px 0px rgba(0,0,0,0.75);
}

//right menu
.right-side {
    text-transform: uppercase;
    display: flex;
    align-items: center;
    justify-content: center;
}
.right-menu {
    position: relative;
}

.right-menu h1 {
    letter-spacing: 2px;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    text-align: center;
    margin-top: 20px;
    margin-bottom: 0px;
    transition: all .4s ease;
}
.portfolio-photo {
    height: 80px;
    width: 80px;
    margin: 0 auto;
    cursor: pointer;
    transition: all 0.4s ease;
}
.separetor-center {
    width: 100px;
    height: 2px;
    background-color: #0d2226;
    margin: 25px auto 25px;
    transition: all .4s ease;
}
.right-menu-more {
    width: 200px;
    height: 50px;
    font-family: 'Poppins', sans-serif;
    box-shadow: 0px 18px 22px 0px rgba(0,0,0,0.75);
    outline: none;
    display: block;
    margin: 0 auto;
}
.right-menu-more {
    background-color: #0d2226;
    border: 3px solid #0d2226;
    color: #fff;
    font-family: 'Poppins', sans-serif;
    font-weight: 900;
    text-transform: uppercase;
    font-size: 11px;
    letter-spacing: 2px;
    word-spacing: 2px;
    cursor: pointer;
    transition: all .6s ease-in-out;
}
.right-menu-more:hover {
    background-color: transparent;
    color: #0d2226;
    box-shadow: 0 11px 27px 0 rgba(0,0,0,.75);
}
.portfolio-photo {
    border: 3px solid #0d2226;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: border .4s;
    box-sizing: border-box;
    transition: all 0.4s ease;
}
.portfolio-img-wrapper {
    height: 62px;
    width: 62px;
    overflow: hidden;
    border-radius: 50%;
    transition: all .4s ease;
}
.portfolio-img-wrapper img {
    height: 62px;
    width: 62px;
    transition: all .4s ease;
}

.active-circle {
    border: 5px solid #000;
}
.active-circle .portfolio-img-wrapper  {
    height: 57px;
    width: 57px;
}
.active-circle .portfolio-img-wrapper  img {
    height: 57px;
    width: 57px;
}
.main-menu-social {
    width: 241px;
    position: absolute;
    top: -100px;
    display: flex;
    justify-content: space-between;
    transition: top 0.2s ease-in-out;
}
.social-btn-open-right-menu {
    top: -62px;
}
.main-menu-social img {
    height: 32px;
    width: 32px;
}

.main-menu-social div {
    height: 32px;
    width: 32px;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 0px 18px 22px 0px rgba(0,0,0,0.75);
}
.insa-position {
    left: -1000px;
    position: relative;
    transition: left .4s ease;
}
.fb-position {
    top: -1000px;
    position: relative;
    transition: top .4s ease;
}
.twetter-position {
    right: -1000px;
    position: relative;
    transition: right .4s ease;
}


.shown-btn .insa-position {
    left: 0;
}
.shown-btn .fb-position {
    top: 0;
}
.shown-btn .twetter-position {
    right: 0;
}

//content area

.content-area {
    position: absolute;
    height: auto;
    width: 75%;
    background-color: rgb(255, 255, 255);
    top: 0;
    left: 100%;
    transition: left 0.6s ease-in-out;
    padding: 150px 8% 100px 7%;
    box-sizing: border-box;
}
.active-content {
    left: 25%;
}

// стилі для правої сторони
.right-side-navigation {
    display: flex;
    justify-content: center;
    position: absolute;
    top: 100px;
    overflow: hidden;
}

.right-side-navigation img{
    height: 16px;
}
.right-side-navigation span {
    display: block;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    padding: 4px 0;
    margin-top: 4px;
    cursor: pointer;
    z-index: 1;
}
.open-content-nav-wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 44px;
    position: relative;
    left: -100%;
    transition: left 0.2s ease-in-out;
}
.show-right-menu {
    left: 0%;
}
.back-arrow-wrp {
    height: 44px;
    width: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0);
    transition: border .4s ease;
}
.back-arrow-wrp:hover{
    border: 1px solid #0d2226;
}

.active-right-menu .portfolio-photo {
    height: 48px;
    width: 48px;
}
.active-right-menu .portfolio-img-wrapper {
    height: 37px;
    width: 37px;
}
.active-right-menu .portfolio-img-wrapper img {
    height: 37px;
    width: 37px;
}
.active-right-menu .separetor-center {
    width: 60px;
    height: 1px;
    margin: 16px auto;
}
.active-right-menu .right-menu-more {
    width: 120px;
    height: 30px;
    font-size: 6px;
}
.active-right-menu .main-menu-social {
    display: none;
}

.active-right-menu .active-circle {
    border: 3px solid #000;
}

// другі соціальні мережі

.open-right-side-social {
    height: 100px;
    position: absolute;
    bottom: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.open-right-side-social img {
    height: 20px;
}
.right-side .open-right-side-social {
    display: none;
}
.active-right-menu .open-right-side-social {
    display: flex;
}

// ліве головне меню

.main-menu-left-active .first-menu-wrapper {
    left: 76%;
}
.main-menu-left-active .left-side {
    width: 24%;
}
.back-arrow-wrp-left {
    height: 44px;
    width: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-sizing: border-box;
    border: 1px solid transparent;

    position: absolute;
    left: -44px;
    transition: all .2s ease-in-out;

}
.back-arrow-wrp-left:hover {
    border: 1px solid #fff;
    transition: border .4s;
}
.back-arrow-wrp-left img {
    height: 16px;
}
.left-contacts-wrapper-arrow{
    display: flex;
    justify-content: center;
    width: 44px;
    position: absolute;

    top: 100px;
    height: 44px;
    overflow: hidden;
}
.back-arrow-wrp-left-active .back-arrow-wrp-left {
    left: 0px;
}