//ширина

@media screen and (max-width: 1328px) {
    .right-menu h1 {
        font-size: 14px;
    }
    .main-menu-social{
        width: 200px;
    }
}
@media screen and (max-width: 966px) {
    .main-menu-left-active .left-menu button {
        width: 160px;
    }
}
@media screen and (max-width: 792px) {
    .main-menu-left-active .left-menu button {
        width: 142px;
    }
    .left-menu-contact{
        margin-bottom: 10px;
    }
    .right-menu h1{
        margin-top: 60px;
    }
    .separetor-center {
        height: 1px;
    }
}
@media screen and (max-width: 680px) {
    .right-menu h1 {
        font-size: 10px;
    }
    .main-menu-left-active .left-menu button {
        width: 130px;
    }
}
@media screen and (max-width: 630px) {
    .right-menu h1 {
        margin-top: 22px;
    }
    .separetor-center {
        width: 50px;
    }
    .main-menu-left-active .left-menu button {
        width: 114px;
    }
}

@media screen and (max-width: 523px) {
    .active-right-menu .right-menu-more {
        width: 84px;
    }
    .main-menu-left-active .left-menu button {
        width: 84px;
        height: 30px;
        font-size: 9px;
        letter-spacing: 1px;
        word-spacing: 1px;
    }
    .main-menu-left-active .left-menu .left-menu-contact {
        border: 1px solid #b4b4b4;
    }
    .main-menu-left-active .left-menu .left-menu-resume {
        border: 1px solid #fff;
    }
}
@media screen and (max-width: 418px) {
    .left-menu button {
        height: 40px;
        width: 144px;
    }
    .right-menu-more {
        height: 40px;
        width: 144px;
    }
    .main-menu-social {
        width: 144px;
    }
    .main-menu-left-active .left-menu button {
        width: 74px;
    }
}
@media screen and (max-width: 386px) {
    .main-page-wrapper-active .right-side {
        width: 20%;
    }
    .right-menu h1 {
        display: none;
    }
    .active-right-menu .separetor-center {
        width: 30px;
    }
    .active-right-menu .right-menu-more {
        width: 48px;
    }
    .right-side-navigation span {
        font-size: 12px;
    }
    .active-right-menu .portfolio-photo{
        border: 1px solid #0d2226;
    }
    .right-menu-more {
        border: 1px solid #0d2226;
    }
    .main-menu-left-active .left-menu button {
        font-size: 6px;
        width: 62px;
    }
}

//висота

@media screen and (max-height: 480px) {
    .open-right-side-social {
        bottom: 40px;
    }
    .right-side-navigation {
        top: 40px;
    }
}